<style scoped>
  .profile{
    width:80px;
    height:80px;
    object-fit: cover;
    object-position: center;
  }
</style>

<template>
    <div class="page-content-wrapper">
      <div class="container"> 
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-3">
          <!-- User Information-->
          <div class="card user-info-card">
            <div class="card-body p-4 d-flex align-items-center justify-content-between">
              <div class="d-flex align-items-center">
                <div class="user-profile me-3">
                  <img :src="profile_pic" :alt="name" v-if="profile_pic" class="profile">
                  <img src="../assets/img/bg-img/9.jpg" alt="" v-else>
                </div>
                <div class="user-info">
                  <p class="mb-0 text-white">{{email ? email : '-'}}</p>
                  <h5 class="mb-0">{{name}}</h5>
                  <p class="mb-0 text-white">{{nra ? nra : '-'}}</p>
                </div>
              </div>
              <div>
                <router-link to="/qr" class="text-white">
                  <i class="lni lni-menu"></i>
                </router-link>
              </div>
            </div>
          </div>
          <!-- User Meta Data-->
          <div class="card user-data-card">
            <div class="card-body">
              <div class="single-profile-data d-flex align-items-center justify-content-between">
                <div class="title d-flex align-items-center"><i class="lni lni-user"></i><span>Full Name</span></div>
                <div class="data-content">{{name}}</div>
              </div>
              <div class="single-profile-data d-flex align-items-center justify-content-between">
                <div class="title d-flex align-items-center"><i class="lni lni-phone"></i><span>Phone</span></div>
                <div class="data-content">{{!isOwn && privatePhone ? '*****' : phone}}</div>
              </div>
              <div class="single-profile-data d-flex align-items-center justify-content-between">
                <div class="title d-flex align-items-center"><i class="lni lni-envelope"></i><span>Email Address</span></div>
                <div class="data-content">{{email ? !isOwn && privateEmail ? '*****' : email : '-'}}</div>
              </div>
              <div class="single-profile-data d-flex align-items-center justify-content-between">
                <div class="title d-flex align-items-center"><i class="lni lni-map-marker"></i><span>Domisili</span></div>
                <div class="data-content">{{domisili ?!isOwn && privateDomisili ? '*****' : domisili : '-'}}</div>
              </div>
              <!-- Edit Profile-->
              <div v-if="isOwn" class="edit-profile-btn mt-3"><router-link class="btn btn-info w-100 text-white" to="/edit-profile"><i class="lni lni-pencil me-2"></i>Edit Profile</router-link></div>
            </div>
          </div>

          <!-- Maps -->
          <div class="card mt-5">
            <div class="card-body p-4 d-flex align-items-center">
              <GmapMap
                    :center="center"
                    :zoom="12"
                    map-type-id="terrain"
                    style="width: 100%; height: 300px"
                  >
                    <GmapMarker
                      :key="index"
                      v-for="(m, index) in markers"
                      :position="m.position"
                      @click="center=m.position"
                    />
                  </GmapMap>
            </div>
          </div>
          
        </div>

      </div>
    </div>
</template>

<script>
    export default {
        name:'ProfileTemplate',
        props:['name','nra','email','phone','domisili','profile_pic','isOwn','privateEmail','privatePhone','privateDomisili'],
        data: () => ({
            center: { lat: -6.200000,  lng: 106.816666 },
            markers:[],
            BASE_URL:process.env.VUE_APP_URL,
        }),
        mounted() {
          this.geolocate()
        },
        methods:{
          geolocate: function() {
            var ths = this
            this.$gmapApiPromiseLazy().then(() => {
              const geocoder = new google.maps.Geocoder()
              geocoder.geocode({'address': this.domisili}, function(results, status) {
                if (status == 'OK') {
                  ths.center = {
                    lat:results[0].geometry.location.lat(),
                    lng:results[0].geometry.location.lng(),
                  }
                    ths.markers.push({ position: ths.center })
                } else {
                  alert('Geocode was not successful for the following reason: ' + status);
                }
              });
            })
            
          },
        }
    }
</script>

<style lang="scss" scoped>

</style>