<style scoped>
  .profile{
    width:80px;
    height:80px;
    object-fit: cover;
    object-position: center;
  }

  .ic{
    width: 30px;
  }

  .clickable:hover{
    cursor: pointer;
  }
</style>

<template>
    <div class="page-content-wrapper">
      <div class="container"> 
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-3" style="min-height:100vh;">
          
          <v-alert dense border="left" type="warning" v-if="!email || !places">
            Mohon Lengkapi data diri anda!
          </v-alert>

          <!-- User Information-->
          <div class="card user-info-card">
            <div class="card-body p-4 d-flex align-items-center justify-content-between">
              <div class="d-flex align-items-center">
                <div class="user-profile me-3">
                  <img :src="profile_pic" :alt="name" v-if="profile_pic" class="profile">
                  <img src="../assets/img/bg-img/9.jpg" alt="" v-else>
                </div>
                <div class="user-info">
                  <p class="mb-0 text-white">{{email ? email : '-'}}</p>
                  <h5 class="mb-0">{{name}}</h5>
                  <p class="mb-0 text-white">{{nra ? nra : '-'}}</p>
                </div>
              </div>
              <div>
                <router-link to="/qr" class="text-white">
                  <i class="lni lni-menu"></i>
                </router-link>
              </div>
            </div>
          </div>

          <div class="card user-data-card">
            <div class="card-body">
              <div class="row">
                <div class="col-3 text-center">
                  <router-link to="/profile">
                        <img src="../assets/icons/profile.png" class="ic" alt="profile-icon">
                        <p>Profile</p>
                  </router-link>
                </div>
                <div class="col-3 text-center">
                  <router-link to="/search">
                        <img src="../assets/icons/cari temen.png" class="ic" alt="cari temen-icon">
                        <p>Cari Teman</p>
                  </router-link>
                </div>
                <div class="col-3 text-center">
                  <img src="../assets/icons/nearme.png" class="ic" alt="nearme-icon">
                  <p>Near Me</p>
                </div>
                <div class="col-3 text-center cursor-pointer" @click="openUrl('https://gerai.ikarholaz.id')">
                  <img src="../assets/icons/card center.png" class="ic" alt="card center-icon">
                  <p>Card Center</p>
                </div>
                <div class="col-3 text-center cursor-pointer" @click="openUrl('https://donasi.ikarholaz.id')">
                  <img src="../assets/icons/fundrising.png" class="ic" alt="funrising-icon">
                  <p>Fundrising</p>
                </div>
                <div class="col-3 text-center cursor-pointer" @click="openUrl('https://klikgeni.com/#/')">
                  <img src="../assets/icons/klikgeni marketplace.png" class="ic" alt="marketplace-icon">
                  <p>Marketplace</p>
                </div>
                <div class="col-3 text-center">
                  <img src="../assets/icons/warung rholaz.png" class="ic" alt="warung-icon">
                  <p>Warung Rholaz</p>
                </div>
                <div class="col-3 text-center cursor-pointer" @click="openUrl('https://listing.ikarholaz.id/')">
                  <img src="../assets/icons/direktory.jpg" class="ic" alt="direktory-icon">
                  <p>Directory</p>
                </div>
                <div class="col-3 text-center">
                  <router-link to="/news">
                    <img src="../assets/icons/feed.png" class="ic" alt="feed-icon">
                    <p>News</p>
                  </router-link>
                </div>
                <div class="col-3 text-center">
                  <img src="../assets/icons/event.jpg" class="ic" alt="event-icon">
                  <p>Event</p>
                </div>
                <div class="col-3 text-center">
                  <router-link to="/edit-profile#business-section">
                    <img src="../assets/icons/add bisnis.png" class="ic" alt="add bisnis-icon">
                    <p>Add Bisnis</p>
                  </router-link>
                </div>
                <div class="col-3 text-center">
                  <img src="../assets/icons/add-komunitas.png" class="ic" alt="add-komunitas-icon">
                  <p>Komunitas</p>
                </div>
              </div>
            </div>
          </div>

          <h5 class="my-5">Kabar Alumni</h5>

          <div class="card mt-5" v-if="kabar_alumni.length">
            <img :src="kabar_alumni[0]._embedded['wp:featuredmedia'][0].source_url" alt="" class="card-img-top">
            <div class="card-body">
              <ol>
                <li v-for="(kabar,i) in kabar_alumni" :key="'kabar'+i">
                  <a :href="kabar.link" target="_blank">{{kabar.title.rendered}}</a>
                </li>
              </ol>
            </div>
          </div>

          <h5 class="my-5">Agenda Alumni</h5>

          <div class="card card-body">
            <div class="row" v-if="agendas">
              <div class="col-6">
                <img :src="STORAGE_URL+'/public/'+agendas[0].image" alt="" class="img-fluid">
              </div>
              <div class="col-6">
                <div v-for="(agenda,i) in agendas" :key="'agenda'+i" class="mb-3">
                  <p class="m-0">{{new Date(agenda.start_time).toLocaleDateString("id-ID",options_date)}}, {{agenda.place}}</p>
                  <h6>{{agenda.name}}</h6>
                </div>
              </div>
            </div>
          </div>

          <h5 class="my-5">Jolali</h5>
          <div class="card card-body">
            <div class="row" v-for="(jolali,i) in jolalis" :key="'jolali'+i">
              <div class="col-4">
                <img :src="STORAGE_URL+'/public/'+jolali.image" alt="" class="img-fluid">
              </div>
              <div class="col-8">
                <a target="_blank" :href="jolali.link">
                  <h6>{{jolali.text}}</h6>
                </a>
              </div>
            </div>
          </div>
          
        </div>

      </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name:'LandingTemplate',
        props:['name','nra','email','places','profile_pic'],
        data: () => ({
            BASE_URL:process.env.VUE_APP_URL,
            STORAGE_URL:process.env.VUE_APP_STORAGE_URL,
            kabar_alumni:[],
            options_date:{ year: 'numeric', month: 'short', day: 'numeric' },
            agendas:[],
            jolalis:[],
        }),
        created(){
          this.getKabarAlumni()
          this.getAgenda()
          this.getJolali()
        },
        methods:{
          openUrl(url) {
            window.open(url, "_blank");
          },
          async getKabarAlumni(){
            let res = await axios.get("https://ikarholaz.com/wp-json/wp/v2/posts?tags=115&_embed")
            this.kabar_alumni = res.data
          },
          async getAgenda(){
            let res = await axios.get(this.BASE_URL+"/api/get-agenda")
            this.agendas = res.data
          },
          async getJolali(){
            let res = await axios.get(this.BASE_URL+"/api/get-jolali")
            this.jolalis = res.data
          }
        }
    }
</script>

<style lang="scss" scoped>
  .cursor-pointer{
    cursor: pointer;
  }
</style>